.event-list {
  flex-flow: wrap;
  justify-content: center;
  width: 100vw;
  padding-bottom: 4em;
  display: flex;
}

.event-tile {
  background-color: var(--area-color);
  opacity: .9;
  text-wrap: wrap;
  border-radius: 12px;
  grid-template-rows: 100%;
  display: grid;
}

.event-details {
  text-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.event-details-icons {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.event-details-title, .event-details-footer {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.event-details-footer-area {
  text-align: center;
  background-color: var(--area-color);
  border: 2px solid #000;
  border-radius: 10px;
}

.event-details-description {
  align-items: center;
}

.event-details-description-more {
  background-color: var(--area-color);
  color: #035980;
  border: none;
}

.event-details-favorites-svg {
  fill: none;
  stroke: currentColor;
  stroke-width: 2px;
  stroke-linecap: round;
  stroke-linejoin: round;
  cursor: pointer;
}

.favorited {
  fill: gold;
  color: gold;
  filter: drop-shadow(1px 2px 1px #0006);
}

@media only screen and (width <= 600px) {
  .event-tile {
    grid-template-columns: 2.5em auto;
    width: 90vw;
    height: auto;
    margin: 10px auto;
    padding: .8em;
  }

  .event-details {
    max-width: 82vw;
    padding-left: .7em;
  }

  .event-details-icons {
    font-size: 1.3em;
  }

  .event-details-title {
    height: 2em;
    font-size: 18px;
    font-weight: 500;
  }

  .event-details-title-left {
    font-size: 1.5em;
  }

  .event-details-fav {
    font-size: 1.5rem;
  }

  .event-details-footer {
    min-height: 1.6em;
    font-size: 1.1em;
    font-weight: 400;
  }

  .event-details-footer-area {
    padding: .3em;
  }

  .event-details-footer-where {
    max-width: 50%;
  }

  .event-details-description {
    min-height: 2em;
    padding-top: .7em;
    padding-bottom: .4em;
    font-size: 1.2em;
    font-weight: 200;
  }

  .event-details-description-more {
    font-size: 1em;
  }

  .event-details-favorites-svg {
    width: 1em;
    height: 1em;
    font-size: 1.3rem;
  }
}

@media only screen and (width >= 600px) {
  .event-tile {
    grid-template-columns: 2.5em auto;
    width: 43vw;
    max-width: 25em;
    height: auto;
    margin: .5em;
    padding: .8em;
  }

  .event-details {
    padding-left: .7em;
  }

  .event-details-icons {
    font-size: 1.3em;
  }

  .event-details-title {
    height: 2em;
    font-size: 18px;
    font-weight: 500;
  }

  .event-details-title-left {
    font-size: 1.5em;
  }

  .event-details-fav {
    font-size: 1.5rem;
  }

  .event-details-footer {
    min-height: 1.6em;
    font-size: 1.1em;
    font-weight: 400;
  }

  .event-details-footer-area {
    padding: .3em;
  }

  .event-details-footer-where {
    max-width: 50%;
  }

  .event-details-description {
    min-height: 2em;
    padding-top: .7em;
    padding-bottom: .4em;
    font-size: 1.2em;
    font-weight: 200;
  }

  .event-details-description-more {
    font-size: 1em;
  }

  .event-details-favorites-svg {
    width: 1em;
    height: 1em;
    font-size: 1.3rem;
  }
}

.menu-favorites-svg {
  fill: none;
  stroke: currentColor;
  stroke-width: 2px;
  stroke-linecap: round;
  stroke-linejoin: round;
  cursor: pointer;
  font-size: 1.5rem;
}

.menu-header-wrapper {
  z-index: 1000;
  height: 4em;
  position: sticky;
  top: 0;
  box-shadow: 0 -10px 7px 7px #000;
}

.menu-header-content {
  cursor: pointer;
  background-color: #d3d3d3;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  max-width: 100vw;
  height: 4em;
  padding: 0 .7em;
  display: flex;
}

.menu-header-icon-wrapper {
  align-items: center;
  display: flex;
}

.menu-header-svg {
  width: 2rem;
  height: 2rem;
  margin: .2em;
  font-size: 1.5rem;
  transition: transform .3s;
}

.menu-accordian-drawer {
  background-color: #d3d3d3;
  flex-direction: column;
  justify-content: space-between;
  min-height: 10em;
  display: flex;
}

.menu-accordian-contact {
  border: none;
  margin-top: 1em;
  font-size: 1em;
}

.menu-accordian-links {
  justify-content: space-between;
  display: flex;
}

.menu-filter-container {
  flex-direction: column;
  font-size: 1.3em;
  display: flex;
}

.menu-filter-label {
  justify-content: space-between;
  margin-bottom: .5em;
  display: flex;
}

.menu-filter-checkbox {
  color: #035980;
  border-radius: .4em;
  width: 3em;
  padding: .2em .4em;
  font-family: Futura, sans-serif;
}

.menu-filter-checkbox:active {
  color: #d3d3d3;
  background: #035980;
}

.menu-filter-checkbox:after {
  color: #035980;
  background: #d3d3d3;
}

.expanded {
  transform: rotate(90deg);
}

.closed {
  transform: rotate(180deg);
}

.search-input-wrapper > input {
  border: 1px solid #000;
  border-radius: 20px;
  font-size: 2em;
}

@media only screen and (width <= 600px) {
  .menu-accordian-drawer {
    border-radius: 5px;
    width: 100vw;
    max-width: 40em;
    margin: 0 auto;
    padding: 1em 0;
  }

  .menu-accordian-contact {
    padding: 0 1rem;
  }

  .menu-filter-container {
    width: 80vw;
    margin: 0 auto;
  }

  .menu-filter-label {
    width: 80vw;
  }

  .search-input-wrapper > input {
    width: 60vw;
    padding: 0 .5em;
  }
}

@media only screen and (width >= 600px) {
  .menu-accordian-drawer {
    border-radius: 5px;
    width: 20em;
    max-width: 40em;
    margin: 0 .5em 0 auto;
    padding: 1em;
  }

  .menu-filter-container, .menu-filter-label {
    width: 40vw;
  }

  .search-input-wrapper > input {
    width: 80vw;
    margin: 0 auto;
    padding: 0 .5em;
  }
}

.footer-tab-wrapper {
  background-color: #d3d3d3;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 100vw;
  height: 2em;
  display: flex;
  position: fixed;
  bottom: 0;
  box-shadow: 0 10px 7px 7px #000;
}

.footer-tabs {
  font-family: Futura Bold, sans-serif;
  font-size: 1em;
  font-variant-caps: small-caps;
}

.footer-tabs > button {
  font: inherit;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 1em;
}

.footer-tab-active {
  color: #d3d3d3;
  background-color: #035980;
  border-radius: 10px;
  font-family: Futura Bold, sans-serif;
  font-size: 1em;
  font-variant-caps: small-caps;
}

.footer-tab-active > button {
  color: #d3d3d3;
  font: inherit;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  margin: 0;
  padding: 1em;
}

@font-face {
  font-family: Futura Bold;
  src: url("Futura Bold.2013bcc4.otf") format("opentype");
}

* {
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
}

html, body {
  background-color: #262626;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}
